<template>
  <div class="content-changePassword">
    <headerUsually btnShow="true" titleText="密码修改" url="/mobileOrdering/home"></headerUsually>
    <div class="list">
      <div class="item">
        <div>原密码</div>
        <input class="input" type="password" v-model="originalPSW" placeholder="请输入原密码">
      </div>
      <div class="item">
        <div>新密码</div>
        <input class="input" type="password" v-model="newPSW" placeholder="4-20位数字或大小写字母组合">
      </div>
      <div class="item">
        <div>确认新密码</div>
        <input class="input" type="password" v-model="confirmPSW" placeholder="请确认新密码">
      </div>
    </div>
    <div class="btn" @click="okFun()">确定</div>
  </div>
</template>

<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast } from "mint-ui";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
export default {
  components: {
    headerUsually
  },
  data() {
    return {
      originalPSW: "",
      newPSW: "",
      confirmPSW: ""
    };
  },
  methods: {
    async okFun() {
      let reg = /^[0-9a-zA-Z]*$/g;
      if (
        !reg.test(this.originalPSW) ||
        this.originalPSW.length > 20 ||
        this.originalPSW.length < 4
      ) {
        this.msg("原始密码为：4-20位数字或大小写字母组合!");
        return;
      }
      let reg2 = /^[0-9a-zA-Z]*$/g;
      if (
        !reg2.test(this.newPSW) ||
        this.newPSW.length > 20 ||
        this.newPSW.length < 4
      ) {
        this.msg("新密码为:4-20位数字或大小写字母组合!");
        return;
      }
      if (this.newPSW != this.confirmPSW) {
        this.msg("新密码与确认密码不一致！");
        return;
      }
      let data = {
        params: {
          oldPwd: this.originalPSW,
          newPwd: this.newPSW
        }
      };
      let res = await ajaxGet("/pos/router?method=updatePwd", data);
      if (res && res.resultCode == 0) {
        this.msg(res.resultMsg);
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>

<style lang="less" scoped>
.content-changePassword {
  padding-top: 46px;
  .list {
    background: #ffffff;
    padding-left: 15px;
    margin-bottom: 20px;
    font-size: 14px;
    color: #666666;

    .item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid #ddd;
      padding: 17px 15px 17px 0;
      .input {
        flex: 1;
        text-align: right;
        outline: none;
        border: none;
        color: #666666;
      }
    }
    .item:last-child {
      border-bottom: none;
    }
  }
  .btn {
    margin-top: 20px;
    background: #ffffff;
    font-size: 18px;
    color: #c4ad68;
    text-align: center;
    padding: 11px;
  }
}
</style>



